	module.exports = {"data":{"wordpressPost":{"title":"The Fashion Girl&#8217;s Guide To All Things Pink And Grey &#8230; Let&#8217;s SHOP!","content":"<p>I love a bright, bold colour as much as anyone but when I shop I find myself gravitating towards the softer, more gentle shades, in particular a dusty soft pink and dove grey.</p>\n<p>I&#8217;m not sure what a colour specialist or, indeed, even a psychologist would make of that but here&#8217;s my amateur take on the pink and grey attraction. <strong>These two shades are not just stylish, but super soothing</strong>. They make you feel cosy and warm even when life is nowhere near either of those things.</p>\n<p>So as I said above &#8211; let&#8217;s shop. And for perfect pinks and greys. Because we all need a little cosy and warm in our lives.</p>\n<p>&nbsp;</p>\n<img class=\"alignnone size-medium wp-image-2861\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150445/Nike-Internationalist-SD-150-JJPG-300x165.jpg\" alt=\"\" width=\"300\" height=\"165\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150445/Nike-Internationalist-SD-150-JJPG-300x165.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150445/Nike-Internationalist-SD-150-JJPG.jpg 603w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p><a href=\"https://store.nike.com/au/en_gb/pd/internationalist-sd-shoe/pid-11623023/pgid-11657089\">Nike Internationalist SD Woman’s Shoe, $150.</a></p>\n<p>Because it&#8217;s the sweetest shoe EVER. No I am not exaggerating. Sweetest &#8211; EVER.</p>\n<p>&nbsp;</p>\n<img class=\"alignnone size-medium wp-image-2863\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150514/Kikki-K-pin-JPG-284x300.jpg\" alt=\"\" width=\"284\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150514/Kikki-K-pin-JPG-284x300.jpg 284w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150514/Kikki-K-pin-JPG.jpg 417w\" sizes=\"(max-width: 284px) 100vw, 284px\" /> <img class=\"alignnone size-medium wp-image-2864\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150526/Morrison-pant-JPG-144x300.jpg\" alt=\"\" width=\"144\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150526/Morrison-pant-JPG-144x300.jpg 144w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150526/Morrison-pant-JPG.jpg 286w\" sizes=\"(max-width: 144px) 100vw, 144px\" /> <img class=\"alignnone size-medium wp-image-2865\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150537/grey-KMart-chair-JPG-264x300.jpg\" alt=\"\" width=\"264\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150537/grey-KMart-chair-JPG-264x300.jpg 264w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150537/grey-KMart-chair-JPG.jpg 399w\" sizes=\"(max-width: 264px) 100vw, 264px\" />\n<p><a href=\"http://www.kikki-k.com/pin-badge-roller-skate-cute-2018\">Kikki.K Roller Skate Pin, $7.95.</a> I don&#8217;t think we need to explain why! <a href=\"https://morrisonshop.com/pants/becker-pant.html\">Morrison Becker Pant, $209. </a>The perfect pants for 2017. Maybe ever and the <a href=\"http://www.kmart.com.au/webapp/wcs/stores/servlet/ProductDisplay?storeId=10701&amp;errorViewName=ProductDisplayErrorView&amp;urlLangId=-1&amp;productId=1629904&amp;urlRequestType=Base&amp;langId=-1&amp;catalogId=10102\">K-Mart Acapulco Replica Chair in Grey, $39</a>. Style at a bargain-basement price. We LOVE.</p>\n<img class=\"alignnone size-medium wp-image-2866\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150548/Seed-clutch-JPG-300x202.jpg\" alt=\"\" width=\"300\" height=\"202\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150548/Seed-clutch-JPG-300x202.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150548/Seed-clutch-JPG.jpg 480w\" sizes=\"(max-width: 300px) 100vw, 300px\" /> <img class=\"alignnone size-medium wp-image-2867\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150601/Cotton-on-bralet-JPG-223x300.jpg\" alt=\"\" width=\"223\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150601/Cotton-on-bralet-JPG-223x300.jpg 223w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150601/Cotton-on-bralet-JPG.jpg 512w\" sizes=\"(max-width: 223px) 100vw, 223px\" /> <img class=\"alignnone size-medium wp-image-2868\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150613/CR-dress-JPG-157x300.jpg\" alt=\"\" width=\"157\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150613/CR-dress-JPG-157x300.jpg 157w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150613/CR-dress-JPG.jpg 333w\" sizes=\"(max-width: 157px) 100vw, 157px\" />\n<p><a href=\"http://www.seedheritage.com/p/sara-ring-clutch/5094061-688-OS-se.html#start=\">Seed Heritage Sara Ring Clutch in Blush, $59.95.</a> Simple, chic, stylish.<br />\n<a href=\"http://cottonon.com/AU/p/body/candice-bralette/9351533398487.html?region=AU#prefn1=countrySpecificMarkets&amp;pmin=0&amp;prefv1=AU&amp;region=AU&amp;start=1\">Cotton On Candice Bralette in Dusty Blush, $24.95.</a> Subtly sexy and oh so comfortable too. <a href=\"https://www.countryroad.com.au/shop/woman/clothing/new-in/60216038/Soft-Pleated-Dress.html\">Country Road Soft Pleated Dress, $249 in Rose.</a> Flattering on all shapes, easy to wear and never fails to impress when I wear it out. Honestly. People lose it over this dress.</p>\n<img class=\"alignnone size-medium wp-image-2869\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150626/Towel-JPG-282x300.jpg\" alt=\"\" width=\"282\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150626/Towel-JPG-282x300.jpg 282w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150626/Towel-JPG.jpg 493w\" sizes=\"(max-width: 282px) 100vw, 282px\" />\n<p><a href=\"https://www.hardtofind.com.au/142521_personalised-embroidered-turkish-towel\">Personalised Embroidered Turkish Towel by Home by Merci Maman, $89 each (in Fuchsia Pink and Medium Grey).</a> There&#8217;s nothing more perfect for a picnic than a Turkish towel and these ones are the best we&#8217;ve seen so far.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/13150409/Nike-trip-pink-grey-JPG.jpg"},"categories":[{"slug":"lifestyle","name":"Lifestyle"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"28185acf-bbdf-575a-ac6f-464593f2e4a5"}}

